<template>
  <div id="app">
    <div id="nav" :class="{ 'horizontal-nav': isSmallScreen }">
      <router-link to="/">
        <font-awesome-icon :icon="['fab', 'pied-piper']" />
      </router-link>
      <div class="main-links">
        <router-link to="/" :class="{ active: selected == 1 }">
          <font-awesome-icon icon="home" @click="selected = 1" />
        </router-link>
        <router-link to="/about" :class="{ active: selected == 2 }">
          <font-awesome-icon icon="layer-group" @click="selected = 2" />
        </router-link>
        <router-link to="/lists" :class="{ active: selected == 3 }">
          <font-awesome-icon icon="folder" @click="selected = 3" />
        </router-link>
        <router-link to="/archive" :class="{ active: selected == 4 }">
          <font-awesome-icon icon="archive" @click="selected = 4" />
        </router-link>
      </div>

      <router-link to="/about">
        <font-awesome-icon icon="cog" />
      </router-link>
    </div>
    <div class="row" :class="{ 'router-parent-margin-left': !isSmallScreen }">
      <div class="router-page col-md-9">
        <transition name="slideUp" appear mode="out-in">
          <router-view class="container" />
        </transition>
      </div>
      <div
        :class="{ container: isSmallScreen, 'bg-white': !isSmallScreen }"
        class="col-md-3"
      >
        <PersonalInfo :isSmallScreen="isSmallScreen"></PersonalInfo>
      </div>
    </div>
  </div>
</template>

<script>
import PersonalInfo from "@/components/PersonalInfo.vue";

export default {
  data() {
    return {
      isSmallScreen: false,
      selected: 1
    };
  },
  methods: {
    checkSmallScreenResized() {
      if (window.innerWidth <= 767 && this.isSmallScreen === false) {
        this.isSmallScreen = true;
      } else if (window.innerWidth > 767 && this.isSmallScreen === true) {
        this.isSmallScreen = false;
      }
    },
    switchPage() {
      this.$parent().remove();
    }
  },
  components: {
    PersonalInfo
  },
  created() {
    if (window.innerWidth <= 767) this.isSmallScreen = true;
    window.addEventListener("resize", this.checkSmallScreenResized);
  },
  destroyed() {
    window.removeEventListener("resize", this.checkSmallScreenResized);
  }
};
</script>

